/*-----Back to Top-----*/
#back-to-top {
  background: #3366ff;
  color: #fff;
  position: fixed;
  bottom: 20px;
  right: 20px;
  z-index: 99;
  display: none;
  text-align: center;
  border-radius: 10px;
  -moz-border-radius: 10px;
  -webkit-border-radius: 10px;
  -o-border-radius: 10px;
  z-index: 10000;
  height: 50px;
  width: 50px;
  line-height: 45px;
  background-repeat: no-repeat;
  background-position: center;
  transition: background-color 0.1s linear;
  -moz-transition: background-color 0.1s linear;
  -webkit-transition: background-color 0.1s linear;
  -o-transition: background-color 0.1s linear;
}

#back-to-top i {
  padding-top: 15px;
  font-size: 16px;
}

#back-to-top:hover {
  background: #fff;
  color: #3366ff;
  fill: #3366ff;
  border: 2px solid #3366ff;
}

#back-to-top:hover .svg-icon {
  fill: #3366ff;
}

#popup {
  position: absolute;
  width: auto;
  height: 30px;
  background: #fe6b1f;
  display: none;
  color: white;
  border-radius: 5px;
}

#copy {
  background: none;
  color: white;
  font-weight: bold;
  padding: 8px 25px;
  border: 0;
}

.countdown span:first-child {
  font-size: 30px;
  font-weight: 500;
}

.social i {
  font-size: 40px;
}

.social .social-icon i {
  font-size: 14px;
}

.widget-info i {
  width: 100px;
  height: 100px;
  padding: 27px 0;
  text-align: center;
  border: 2px solid #fff;
  border-radius: 50%;
}

.widget-info a {
  border-bottom: 1px solid #fff;
}

.about-img {
  background: url("../images/about.jpg");
  background-size: cover;
  background-position: center;
