  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
}

.login-bg1 {
  background: url("../images/photos/login2.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.login-bg1::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(6, 10, 48, 0.85);
}

.login-bg1 .custom-logo-dark, .customlogin-content .custom-logo-dark {
  display: none;
}

.customlogin-imgcontent {
  position: absolute;
  top: 35%;
  padding: 5rem;
}

.error-bg1 {
  background: url("../images/photos/error.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.error-bg1::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(51, 102, 255, 0.7);
}

.construction-bg {
  background: url("../images/pattern/2.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.construction-bg::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(51, 102, 255, 0.85);
}

.construction .construction-search {
  background: transparent;
  border-radius: 50px;
}

.construction .form-control {
  border: 1px solid rgba(255, 255, 255, 0.1);
  color: #fff;
  height: 40px;
  background: rgba(0, 0, 0, 0.1);
  border-radius: 50px;
}

.construction .form-control:focus {
  border: 1px solid rgba(255, 255, 255, 0.1);
  -webkit-box-shadow: none !important;
          box-shadow: none !important;
  color: #fff;
  background: transparent;
}

.construction .construction-search .btn {
  position: absolute;
  right: 18px;
  top: 2px;
  color: rgba(255, 255, 255, 0.3);
}

